.maquina-escribir {
  background-color: yellow;
  position: relative;
}

.maquina-escribir::after {
  content: "";
  /* border: 2px solid white; */
  background-color: red;
  right: 0;

  height: 100%;
  position: absolute;
  animation: maquinaescribir 2s infinite alternate steps(19);
}

@keyframes maquinaescribir {
  from {
    width: 100%;
  }
  to {
    width: 0;
  }
}
